export var calculatePercentRounded = function (value, target) {
    var percent = (value / target) * 100;
    if (percent === Number.POSITIVE_INFINITY || Number.isNaN(percent)) {
        return 0;
    }
    return Math.floor((percent < 1 ? Math.ceil(percent) : Math.floor(percent)));
};
export var formatMoney = function (value) {
    var parsed = Number.parseFloat(((Math.round(value * 100)) / 100).toString()).toFixed(2);
    if (Number.isNaN(parseFloat(parsed)) || isNaN(value)) {
        return 0.00;
    }
    return parsed;
};
export var camelize = function (str) { return (str.toString().replace(/(?:^\w|[A-Z]|\b\w)/g, (function (word, index) { return (index === 0 ? word.toLowerCase() : word.toUpperCase()); })).replace(/\s+/g, '')); };
